@import url("../fonts/MuseoSansCyrl.otf");html{width:100%;height:100%;box-sizing:border-box}@media (min-width: 320px){html{font-size:50.1%}}@media (min-width: 768px){html{font-size:56.3%}}@media (min-width: 992px){html{font-size:62.5%}}body{width:100%;height:auto;font-size:1.6rem;font-family:'MuseoSansCyrl', sans-serif;margin:0;background-size:cover;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}body ::-webkit-scrollbar{width:8px;background-color:rgba(146,146,146,0)}body ::-webkit-scrollbar-track{border-radius:4px}body ::-webkit-scrollbar-thumb{border-radius:4px;background-color:rgba(180,180,180,0.8)}body ::-webkit-scrollbar-thumb:hover{background-color:rgba(130,130,130,0.6);cursor:pointer}body ::-webkit-scrollbar-thumb:active{background-color:rgba(130,130,130,0.9);cursor:pointer}body input[type=number]::-webkit-inner-spin-button,body input[type=number]::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}a:hover{text-decoration:none}strong{font-weight:bold}a,button{cursor:pointer}input:focus,select:focus,textarea:focus,button:focus{outline:none}section{position:relative}.btn-primary:not(:disabled):not(.disabled).active:focus,.btn-primary:not(:disabled):not(.disabled):active:focus,.show>.btn-primary.dropdown-toggle:focus{box-shadow:none}
